<template>
  <div class="page-not-found">
    <div class="header">
      <div class="left">
        <!-- <div class="title">{{ texts.label1 }}<br>{{ texts.label2 }}</div> -->
      </div>
      <div class="right">
        <!-- <img src="https://bin.bb-os.com/_nuxt/img/icon-404.12408e8.svg" alt="" /> -->
      </div>
    </div>
  </div>
</template>

<script>
import { LANG_KEY } from '@/utils'
export default {
  props: {
    lang: String,
  },

  computed: {
    texts() {
      if(this.lang === LANG_KEY.tw) {
        return {
          label1: '這是',
          label2: '未知的水域。',
        }
      } else if (this.lang === LANG_KEY.zh) {
        return {
          label1: '这是',
          label2: '未知的水域。',
        }
      } else {
        return {
          label1: 'These are',
          label2: 'uncharted waters.',
        }
      }
    }
  },

  created() {
    if(location.href.includes('%23')) {
      location.replace(decodeURIComponent(location.href))
    }
  }
}
</script>

<style scoped>
.page-not-found {
  max-width: 1200px;
  margin: 100px auto;
}
.header {
  padding: 80px 0 60px;
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
}
.left {
  display: flex;
  flex-direction: column;
  background: url(https://bin.bb-os.com/_nuxt/img/icon-bg.7589fa8.svg) 50% no-repeat;
  background-size: contain;
  width: 647px;
  box-sizing: border-box;
  color: #262626;
  margin-right: 113px;
  padding-left: 66px;
}
.title {
  font-weight: 800;
  font-size: 42px;
  line-height: 54px;
  margin-bottom: 34px;
}
.right {
  display: flex;
}
</style>


